.loader_text {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 4rem;
}

.loader_element {
  position: absolute;
  width: 10%;
  box-shadow: 0px 0px 16px 9px rgb(255 0 0 / 75%);
  animation: 2s cubic-bezier(0.4, 0, 1, 1) 0s infinite alternate knight_rider;
}

@keyframes knight_rider {
  from {
    margin-left: -100%;
  }
  to {
    margin-left: 100%;
  }
}
